.Container {
    position: relative;
    min-height: 300px;
    max-height: 100%;
    overflow: hidden;
}

/* Style the video: 100% width and height to cover the entire window */
.Video {
    width: 100%;
    height: 60%;
    overflow: hidden;
  }
  
  /* Add some content at the bottom of the video/page */
  .Content {
    position: absolute;
    bottom: 0;
    padding-top: 120%;
    /* color:#f1f1f1; */
    /* background-color: rgba(0, 0, 0, 0.3); */
    width: 100%;
    height: 100%;
    display: flex; 
    /* justify-content: center;  */
    align-items: flex-start;        
    overflow: hidden;
   } 

  .SubContent button {
      font-weight: bold;
      margin-top: 30px;
      color: #f1f1f1;
      border: #f1f1f1 1px solid;
      padding: 10px 5px;
      background-color: hsla(0, 0%, 0%, 0);
      font-size: 1.25rem;
      border-radius: 5px;
  }
  .SubContent button:hover {
      background-color: #f1f1f1;
      color: #000000c0;
  }

  .SubContent img {
    width: 100%;
    height: 30%;
    position: 'relative';
    /* border-radius: 50%; */
    /* display: block; */
 padding-top: 10px;
    /* border: #f1f1f1 1px solid; */
  }

  .Name {
      font-size: 1rem;
    color: white;      
    position: absolute;
    left: 40px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    margin-top: 3px;
   }

   .Dialogue {
       margin-top: 30px;
    font-size: .8rem;
  color: black;      
  position: absolute;
  align-items: left;
  left: 40px;
  right: 40px;
  font-family: Georgia, 'Times New Roman', Times, serif;
 }